$(function ()  {

    //Form submit
    $(".formMail").submit(function() {
        var th = $(this);

        $.ajax({
            type: "POST",
            url: 'mail.php',
            data: th.serialize()
        }).done(function() {
            setTimeout(function() {
                $('.popup').hide();
                if(!th.hasClass('quest__form')) {
                    $('#thanks__popup').show();
                } else {
                    $('#quest__popup').show();
                }
                $('#thanks__popup').fadeIn(400);
                $('body').addClass('overBody');
            }, 200);
            setTimeout(function() {
                $('input[type=text]').val('');
                $('input[type=email]').val('');
                $('textarea').val('');
            }, 300);

        });
        return false;

    });



    if($('.section__feed').length > 0) {
        var bg = document.querySelector('.mouse-parallax-bg');
        var bg1 = document.querySelector('.fr_1');
        var bg2 = document.querySelector('.fr_2');
        var bg3 = document.querySelector('.fr_3');
        var bg4 = document.querySelector('.fr_4');
        var bg5 = document.querySelector('.fr_5');
        window.addEventListener('mousemove', function(e) {
            var
                width = $(window).width();

            if(width > 1024) {
                var x = e.clientX / window.innerWidth;
                var y = e.clientY / window.innerHeight;
                bg.style.transform = 'translate(-' + x * 50 + 'px, -' + y * 50 + 'px)';
                bg1.style.transform = 'translate(' + x * 20 + '%, ' + y * 20 + '%)';
                bg2.style.transform = 'translate(' + x * 20 + '%, ' + y * 20 + '%)';
                bg3.style.transform = 'translate(' + x * 30 + '%, ' + y * 30 + '%)';
                bg4.style.transform = 'translate(' + x * 40 + '%, ' + y * 40 + '%)';
                bg5.style.transform = 'translate(' + x * 30 + '%, ' + y * 40 + '%)';
            }
        });
    }


    if($('.orbit__sirkle').length > 0) {
        var s1 = document.querySelector('.orbit__cir1');
        var s3 = document.querySelector('.orbit__cir3');
        var s4 = document.querySelector('.orbit__cir4');
        window.addEventListener('mousemove', function(e) {
            var
                width = $(window).width();

            if(width > 1024) {
                var x = e.clientX / window.innerWidth;
                var y = e.clientY / window.innerHeight;
                var yx = y + x;
                s1.style.transform = 'rotate(' + yx * 3 + 'deg)';
                s3.style.transform = 'rotate(' + -yx * 3 + 'deg)';
                s4.style.transform = 'rotate(' + yx * 3 + 'deg)';
            }
        });
    }



    //Padv carousell
    if($('.padv__carousel').length > 0) {
        $(document).ready(function() {
            if ( $(window).width() < 600 ) {
                padvCarousell();
            } else {
                $('.padv__carousel').addClass('disabledCar');
            }
        });

        $(window).resize(function() {
            if ( $(window).width() < 600 ) {
                padvCarousell();
                $('.padv__carousel').removeClass('disabledCar');
            } else {
                stopCarousel();
                $('.padv__carousel').addClass('disabledCar');
            }
        });
    }





});


//Open nav
function openNav() {
    event.preventDefault();
    $('body').toggleClass('openedNav');
}



function closePopup() {
    event.preventDefault();

    $('#thanks__popup').fadeOut(400);
    $('body').removeClass('overBody');
}


function sumbitForm($this) {
    event.preventDefault();

    var
        thisForm = $this.closest('form'),
        thisRequired = thisForm.find('.required');

    var errorsCount = 0;
    thisRequired.each(function () {
        var
            input = $(this),
            inputVal = input.val(),
            reqType = input.attr('type');

        if(reqType === 'email') {
            if(!isValidEmailAddress(inputVal)) {
                errorsCount += 1;

                input.addClass('error');
            }
        }
        if(reqType === 'text') {
            if(inputVal.length < 1) {
                errorsCount += 1;

                input.addClass('error');
            }
        }

    });


    if(grecaptcha.getResponse() == "") {
        $('.recaocha__error').addClass('error');
    } else {
        if (errorsCount === 0) {
            thisForm.submit();
        }
        $('.recaocha__error').removeClass('error');
    }

    $('.input, .textarea').focusin(function () {
        $('.error').removeClass('error');
    });

}

function isValidEmailAddress(emailAddress) {
    var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
    return pattern.test(emailAddress);
}




//Button hover
$( ".button_su_inner" ).mouseenter(function(e) {
    var parentOffset = $(this).offset();

    var relX = e.pageX - parentOffset.left;
    var relY = e.pageY - parentOffset.top;
    $(this).prev(".su_button_circle").css({"left": relX, "top": relY });
    $(this).prev(".su_button_circle").removeClass("desplode-circle");
    $(this).prev(".su_button_circle").addClass("explode-circle");

});

$( ".button_su_inner" ).mouseleave(function(e) {

    var parentOffset = $(this).offset();

    var relX = e.pageX - parentOffset.left;
    var relY = e.pageY - parentOffset.top;
    $(this).prev(".su_button_circle").css({"left": relX, "top": relY });
    $(this).prev(".su_button_circle").removeClass("explode-circle");
    $(this).prev(".su_button_circle").addClass("desplode-circle");

});

