// Feed change
function feedCh($this) {
    var
        thisName = $this.find('.feedname').html(),
        thisPos = $this.find('.feed__pos').html(),
        thisRev = $this.find('.feed__rev').html();

    $('.feedrev.active').addClass('trans');
    $this.addClass('active');
    $this.siblings().removeClass('active');

    setTimeout(function() {
        $('.trans').removeClass('trans');
    }, 410);

    $('.feed__revname').html(thisName);
    $('.feed__revpos').html(thisPos);
    $('.feed__revtxt').html(thisRev);
}


///////////
//FAQ
function accCont($this) {
    var
        thisItem = $this.closest('.faq__item'),
        thisAns = thisItem.find('.faq__ansver');

    thisItem.toggleClass('activeAcc');
    thisItem.siblings().removeClass('activeAcc');
    thisItem.siblings().find('.faq__ansver').slideUp(400);
    thisAns.slideToggle(400);
}

function orbitChange($this) {
    var
        thisName = $this.find('.orvit__name').html(),
        thisText = $this.find('.orbit__desc').html();

    $this.addClass('orbitActive');
    $this.siblings().removeClass('orbitActive');
    $('.orbit__pname').html(thisName);
    $('.orbit__ptext').html(thisText);
}


//Padv carousell
function padvCarousell() {
    var owl = $(".padv__carousel").owlCarousel({
        smartSpeed: 500,
        mouseDrag: true,
        autoplay: false,
        center: false,
        margin: 0,
        items: 1,
        addClassActive : true,
        loop: true,
        nav: false,
        dots: true,
        touchDrag: true,
        dotsSpeed: 500,
        stagePadding: 0,
    });

}



function stopCarousel() {
    var owl = $('.padv__carousel');
    owl.trigger('destroy.owl.carousel');
    owl.addClass('disabledCar');
}